import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import "./style.scss";

export const Modal: React.FC<{
  children: React.ReactNode;
  title: string;
  show: boolean;
  onClose: any;
}> = ({ children, title, show, onClose }) => {
  let [display, setDisplay] = useState("hide");
  function disableScroll() {
    // Get the current page scroll position
    const scrollTop = document.documentElement.scrollTop;
    const scrollLeft = document.documentElement.scrollLeft;

    // if any scroll is attempted, set this to the previous value
    (window as any).onscroll = function () {
      window.scrollTo(scrollLeft, scrollTop);
    };
  }

  function enableScroll() {
    window.onscroll = function () {};
  }

  function closeModal() {
    enableScroll();
    setDisplay("hide");
    onClose();
  }

  if (display === "show") disableScroll();

  if (show === true && display === "hide") {
    window.scrollTo(0, 0);
    setDisplay("show");
  }
  return (
    <div className={`modal ${display}`}>
      <div className="modal__overlay"></div>
      <div className="modal__content">
        <div className="modal__content__header">
          <div className="modal__content__header__title">{title}</div>
          <div className="modal__content__header__close" onClick={closeModal}>
            <FontAwesomeIcon icon={faTimes} />
          </div>
        </div>
        <div className="modal__content__children">{children}</div>
      </div>
    </div>
  );
};
