import React, { useState } from "react";
import Card from "../../components/Card";
import LayoutWithMenu from "../../components/LayoutWithMenu";
import { Modal } from "../../Modal";
import imboiliarias from "../../imobiliarias.json";
import "./style.scss";

export default function Home() {
  const [showModal, setShowModal] = useState(false);
  const [cidade, setCidade] = useState("");
  return (
    <LayoutWithMenu>
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(350px, 1fr))",
          gap: "10px",
        }}
      >
        <Card
          title="Financeira"
          text="Veja opções de financiamento"
          buttonText="Saiba mais"
        />
        <Card
          title="Construtora"
          text="Veja nossos modelos de casas"
          buttonText="Saiba mais"
        />
        <Card
          title="Imobiliaria"
          text="Procure uma em sua cidade"
          buttonText="Ver parceiras"
          buttonAction={() => {
            setShowModal(true);
          }}
        />
        <Card
          title="Painéis solares"
          text="Economize em sua conta de luz"
          buttonText="Saiba mais"
        />
        <Modal
          show={showModal}
          onClose={() => {
            setShowModal(false);
          }}
          title="Descubra imobiliárias parceiras em sua cidade"
        >
          <select
            className="select"
            onChange={(event) => setCidade(event.target.value)}
            defaultValue="0"
          >
            <option value="">Selecione a cidade...</option>
            {imboiliarias.map((c) => {
              return (
                <option key={c.nome} value={c.nome}>
                  {c.nome}
                </option>
              );
            })}
          </select>
          <div className="imobiliarias">
            {imboiliarias.map((c) => {
              if (c.nome === cidade) {
                return c.imobiliarias.map((i) => {
                  return (
                    <a
                      className="imobiliarias__item"
                      key={i.nome}
                      href={i.website}
                      target="_blank"
                      rel="noreferrer"
                    >
                      <img
                        src={process.env.PUBLIC_URL + i.imagem}
                        alt={i.nome}
                      />
                    </a>
                  );
                });
              } else return null;
            })}
          </div>
        </Modal>
      </div>
    </LayoutWithMenu>
  );
}
