import React from "react";
import Menu from "./Menu";
import { useAppState } from "../AppContext";

const LayoutWithMenu: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  return (
    <div>
      <Menu />
      <div
        style={{
          width: "90vw",
          margin: "auto",
          paddingTop: "70px",
          height: "calc(100vh - 70xp)",
        }}
      >
        {children}
      </div>
    </div>
  );
};

export default LayoutWithMenu;
