import React from "react";
import "./style.scss";

const Card: React.FC<{
  title: string;
  text: string;
  buttonText: string;
  buttonAction?: any;
}> = ({ title, text, buttonText, buttonAction }) => {
  return (
    <div className="card">
      <div className="card__overlay">
        <div className="card__overlay__content">
          <div className="card__overlay__content__title">{title}</div>
          <div className="card__overlay__content__text">{text}</div>
        </div>
        <div className="card__overlay__actions">
          <button
            className="card__overlay__actions__button"
            onClick={buttonAction}
          >
            {buttonText}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Card;
