import {
  faEnvelope,
  faMapMarkerAlt,
  faPhone,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import LayoutWithMenu from "../../components/LayoutWithMenu";
import "./style.scss";

const About = () => {
  return (
    <LayoutWithMenu>
      <div className="about">
        <div className="about__objectives">
          <img className="about__objectives__image" src="/objetivo.jfif" />
          <div className="about__card about__objectives__text">
            <h2>Nossos objetivos</h2>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat,
              quaerat ipsum enim quibusdam odit dolorem minima culpa,
              praesentium incidunt reiciendis, necessitatibus mollitia
              exercitationem odio eveniet. Quis neque iure ea rerum?
            </p>
          </div>
        </div>
        <div className="about__contact">
          <div className="about__card about__contact__text">
            <h2>Contate-nos</h2>
            <table>
              <tbody>
                <tr>
                  <td>
                    <FontAwesomeIcon icon={faPhone} />
                  </td>
                  <td>14 99999-9999</td>
                </tr>
                <tr>
                  <td>
                    <FontAwesomeIcon icon={faEnvelope} />
                  </td>
                  <td>contato@email.com</td>
                </tr>
                <tr>
                  <td>
                    <FontAwesomeIcon icon={faMapMarkerAlt} />
                  </td>
                  <td>Rua Edgar Ferraz, n. 2480 - Jaú, SP</td>
                </tr>
              </tbody>
            </table>
          </div>
          <img className="about__contact__image" src="/telefone.jfif" />
        </div>
      </div>
    </LayoutWithMenu>
  );
};

export default About;
