import { faBars, faList, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { NavLink, Link } from "react-router-dom";
import "./style.scss";

function Menu() {
  const [clicked, setClicked] = useState(false);
  return (
    <nav className="topbar">
      <div className="topbar__header">
        <div className="topbar__logo">
          <Link to="/">Logo da empresa</Link>
        </div>
        <div
          className="topbar__mobile_button"
          onClick={() => {
            setClicked(!clicked);
          }}
        >
          <FontAwesomeIcon icon={clicked ? faTimes : faBars} />
        </div>
      </div>
      <div
        className={`topbar__menu topbar__menu--${clicked ? "show" : "hide"}`}
      >
        <NavLink
          exact
          activeClassName="topbar__menu__item--active"
          className="topbar__menu__item"
          to="/"
        >
          Home
        </NavLink>
        <NavLink
          exact
          activeClassName="topbar__menu__item--active"
          className="topbar__menu__item"
          to="/sobre"
        >
          Sobre Nós
        </NavLink>

        <NavLink
          exact
          activeClassName="topbar__menu__item--active"
          className="topbar__menu__item"
          to="/parceiros"
        >
          Parceiros
        </NavLink>

        <NavLink
          exact
          activeClassName="topbar__menu__item--active"
          className="topbar__menu__item"
          to="/clientes"
        >
          Clientes
        </NavLink>
      </div>
    </nav>
  );
}

export default Menu;
